.cart {
    overflow-x: auto;
}

.cart table {
    width: 100%;
    border-collapse: collapse;
    table-layout: fixed;
    white-space: nowrap;
}

.cart table img {
    width: 70px;
}

.cart table td:nth-child(1) {
    width: 100px;
}

.cart table td:nth-child(2) {
    width: 120px;
}

.cart table td:nth-child(3) {
    width: 250px;
}

.cart table td:nth-child(4),
.cart table td:nth-child(5),
.cart table td:nth-child(6) {
    width: 150px;
}

.cart table td:nth-child(1),
.cart table td:nth-child(2),
.cart table td:nth-child(3),
.cart table td:nth-child(4),
.cart table td:nth-child(5),
.cart table td:nth-child(6) {
    text-align: center;
}

.cart table td:nth-child(5) input {
    width: 70px;
    padding: 10px 5px 10px 15px;
}

.cart table thead {
    border: 1px solid #e2e9e1;
    border-left: none;
    border-right: none;
}

.cart table thead td {
    font-weight: 700;
    text-transform: uppercase;
    font-size: 13px;
    padding: 18px 0;
}

.cart table thead tr td {
    padding-top: 15px;
}

.cart table thead td {
    font-size: 13px;
}

.cart table tbody .cancelIcon {
    transition: .3s;
}

.cart table tbody .cancelIcon:hover {
    color: #ff3b65;
    cursor: pointer;
}

.cart-add {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.cart-add .coupon {
    width: 50%;
    margin-bottom: 30px;
}

.cart-add .coupon h3,
.subtotal h3  {
    padding-bottom: 15px;
}

.cart-add .coupon input {
    padding: 10px 20px;
    outline: none;
    width: 60%;
    margin-right: 10px;
    border: 1px solid #e2e9e1;
}

.cart-add .coupon button,
.subtotal button {
    background-color: #088178;
    color: #FFF;
    padding: 12px 20px;
    transition: .3s ease;
}

.cart-add .coupon button:hover,
.subtotal button:hover {
    color: #088178;
    background-color: #FFF;
}

.subtotal {
    width: 50%;
    margin-bottom: 30px;
    border: 1px solid #e2e9e1;
    padding: 30px;
}

.subtotal table {
    border-collapse: collapse;
    width: 100%;
    margin-bottom: 20px;
}

.subtotal table td {
    width: 50%;
    border: 1px solid #e2e9e1;
    padding: 10px;
    font-size: 13px;
}

@media(max-width:477px) {
    
    .cart-add {
        flex-direction: column;
    }

    .cart-add .coupon {
        width: 100%;
    }

    .subtotal {
        width: 100%;
        padding: 20px;
    }
}